
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from 'vuex'
export default {
name: 'app',
directives: {
TransferDom
},
computed: {
...mapState({
isLoading: state => state.isLoading
})
},
components: {
Loading,
TransferDom
}
}



